@import url(https://fonts.googleapis.com/css?family=Saira+Semi+Condensed&display=swap);
body {

  font-family: "Saira Semi Condensed", "Cairo", sans-serif;
  text-shadow: black 1px 0 2px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Home{
	height: 100vh;
	width: 100%;
	background-color: #202020;
}
.Particle{ 
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;	
	height: 100%;
	width:100%;
}
.Name{
	padding-top: 40vh;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.image-container{
	height: 100vh;
	width:100%;

}
.data-container{
	margin-top:35vh; 
	margin-left:5%; 
	height: 50vh;
	width:95%;
	position: absolute; 
    letter-spacing: 3px;
}



.container-Animation{
    animation-name: pulse;
	animation-duration: 4s;
	animation-iteration-count: 1;

}
@keyframes pulse {
  from {
    transform: translate(-50%,0);
  }
  to {
    transform: translate(0%,0);
}
}

.container-AnimationIN{
  text-shadow: black 1px 0 10px;
	animation-play-state: running; 

	animation-name: pulses;
	animation-duration: 4s;
	animation-iteration-count: 1;
}

@keyframes pulses {
  
  from {
    transform: rotate(0deg);
} 
  to {
    transform: rotate(360deg);
}

}












.container2-Animation{

	animation-delay: 8s;
    animation-name: pulse2;
	animation-duration: 4s;
	animation-iteration-count: 1;

}

@keyframes pulse2 {
  from {
    transform: translate(-50%,0);
  }
  to {
    transform: translate(0%,0);
}
}
.container2-AnimationIN{

  text-shadow: black 1px 0 10px;
	animation-delay: 8s;
	animation-name: pulses2;
	animation-duration: 4s;
	animation-iteration-count: 1;
}

@keyframes pulses2 {
  
  from {
    transform: rotate(0deg);
} 
  to {
    transform: rotate(360deg);
}
}
























.container3-Animation{

  animation-delay: 17s;
    animation-name: pulse3;
  animation-duration: 4s;
  animation-iteration-count: 1;

}

@keyframes pulse3 {
  from {
    transform: translate(-50%,0);
  }
  to {
    transform: translate(0%,0);
}
}
.container3-AnimationIN{

  text-shadow: black 1px 0 10px;
  animation-delay: 17s;
  animation-name: pulses3;
  animation-duration: 4s;
  animation-iteration-count: 1;
}

@keyframes pulses3 {
  
  from {
    transform: rotate(0deg);
} 
  to {
    transform: rotate(360deg);
}
}


.Logo{
	background-image: url('https://consol.com.pk/wp-content/uploads/2020/10/LOGO-3-E-WEB.png');
	background-size: contain;
	background-repeat: no-repeat; 
	height: 80px;
	width: 90px;
	background-color: none;
	transition: 2s;
}

.Logo:hover{
	transform: translate(0, 10px)  scale(1.5,1.5);
}

.navBar{
	top:0;
	width: 100%;
	position: fixed;
	padding-left: 3%;
	padding-right: 2%;
	padding-top: 5px;
	z-index:5;
	font-weight: bolder;
	opacity: .9;
	background: #9bc3d1;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to left, #0f0e17, #9bc3d1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	border-bottom: 1px #f5f55f solid;

}
.link{
	opacity: 0.9;
}
.link:hover{

}
.navBarInside{
	color: #e8e54f;
    letter-spacing: 3px;
    font-weight: bold;
}
.navBarInside:hover{
	color: yellow;
}
.active{
	color:#ff564a;
	
}
.WhatsappBtn{
	margin-top: 15px;
	border-radius: 50px 50px 50px 0;
	background-color: #25D366;
	border:2px white solid;
	color: white;
  	transition: 1s;
  	padding-top:8px;
  	padding-bottom:8px; 
}
.WhatsappBtn:hover{
	background-color: #1a994a;
	transform:  scale(1.05,1);
}

.MenuBtn{
	display: none;
	height: 35px;
	width: 35px;
	border-radius: 50px;
	background-color: white;
  	animation-name: Menub;
	animation-duration: 2s;
	animation-iteration-count:1;

}

@keyframes Menub {
  from {
	transform: translate(-500px,0);
    opacity: 0;
  }
  to {
	transform: translate(0px,0);
    opacity: 1;
}}
.MenuBtn:hover{
	background-color: orange;
	transform:  scale(1.05,1);
}



.NavList{
	transition: 2s;
  display: block;

}



.DockedMenu{
    display: none;
	height: 100vh;
	width: 50%;
	float: right;
	right: 0;
	position: fixed;
	background-color: white;
	z-index: 6;
	font-weight: bolder;
	opacity: .9;
	background: #9bc3d1;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to left, #0f0e17, #9bc3d1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

	border-left: 1px #f5f55f solid;
	animation: DMenub 3s 1;

}

@keyframes DMenub {
  from {
	transform: translate(500px,0);
    opacity: 0;
  }
  to {
	transform: translate(0px,0);
    opacity: 1;
}}

@keyframes DMenubAfter {
  from {
	transform: translate(0,0);
    opacity: 1;
  }
  to {
	transform: translate(500px,0);
    opacity: 0;
}}

.MenuList{
	transition: 2s;
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  margin-left: 10px;

}

.MenuBarInside{
	color: #e8e54f;
    letter-spacing: 3px;
    font-weight: bold;
}


.DockedList{
	transition: 2s;
  display: block;
  margin-top: 40px;
  margin-left: 10px;
}

.XBtn{
	margin:2px;
	height: 35px;
	width: 35px;
	border-radius: 50px;
	background-color: white;
  
}

.ClientMain{
	background-color: none;
	font-size: 50px;
	text-align: center;
	margin: 0;
	height: 250px;
	border-top: 2px yellow solid;
	
}


.Slider{
	width: 80%;
	margin-right: 10%;
	margin-left: 10%;
}
.ClientImg{
	height: 70px;
	width: 70px;
	margin-left: 80px;
	margin-right: 80px;

}
.ImgBox{
	display: flex;
	flex-direction: row;
    align-self: center;
}
.workMain{

	color: white;
	width: 100%;
	padding-left: 5%;
	padding-right: 5%;
	border-top: 2px yellow solid;
	border-radius: 10px 10px 0 0;
	height: auto;
	background: ##dede6b;
	background-image: url(/consol/static/media/back1.fe2ab837.png), url(/consol/static/media/back2.19fd7aaa.png);
	background-repeat: repeat-x, repeat;

}
.work{
	justify-content: center;
	display: flex;
	flex-direction: row;
	flex-flow: wrap;
}
.workAddressBox{
	margin: 20px;
	width: 400px;
	height: auto;
	margin-right: 30px;
	margin-left: 30px;
	min-width: 150px;

  box-shadow:  black 1px 0 10px;
}
.FooterMain{
	height: auto;
	width: 100%;
	font-size: 50px;
	text-align: center;
	color: white;

	border-top: 2px yellow solid;
	border-radius: 10px 10px 0 0;
	background-color: #0f0e17;
	opacity: 0.9;
	background-image:  radial-gradient(#f39f8f 1.75px, transparent 1.75px), radial-gradient(#f39f8f 1.75px, #0f0e17 1.75px);
	background-size: 70px 70px;
	background-position: 0 0,35px 35px;
	}

.FooterBody{
	justify-content: center;
	display: flex;
	flex-direction: row;	
	width: 100%;
	padding: 2%;
}

.FooterAdresses{
	justify-content: center;
	display: flex;
	flex-direction: row;
	padding-right: 5%;	
    align-self: center;
}

.FooterAddressBox{
	width: 250px;
	margin-right: 10px;
	margin-left: 10px;
	min-width: 150px;
	
  text-shadow: black 1px 0 2px;
}
.FooterSign{
	height: auto;
	width: 100%;
	padding:2px;
	background-color: black;
	opacity: 0.9;
}
.AboutUsFormMain{
    width: 100%;
    height: auto;
    padding: 2%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

  transition: 5s ease-in;
    animation-name: Cclose;
    animation-duration: 3s;
    animation-iteration-count:1;
}

@keyframes Close {
  from {
    height : 0px;
  }
  to {
    height : max-height;
}}

@keyframes EClose {
  from {
  transform: translate(0,0);
    opacity: 1;
  }
  to {
  transform: translate(500px,0);
    opacity: 0;
}}

.formbox{
    padding: 2%;
    max-width:600px;
    min-width:150px;
    width:400px;
}
.field{
    width:100%;
}
.labels{
    font-size: 25px;
    color: grey;
    padding-bottom: 100px;
    transition: 1s;
}
.labels:hover{
    color: black;

}
.fieldinput{
width: 100%;
}




.XFormBtn{
  height: 35px;
  width: 35px;
  border-radius: 50px;
  background-color: white;
  
}
.AboutUsMain{

	color: white;
	width: 100%;
	
	border-top: 2px yellow solid;
	height: auto;
}
.aboutUs{
	justify-content: center;
	display: flex;
	flex-direction: column;
	
}
.aboutUsIN{
	justify-content: center;
	display: flex;
	flex-direction: row;
	height: auto;
	
}
.aboutUsInBox{ 
	height: auto;
	width: 100%;

	min-width: 150px;
	padding: 2%;
	color:black;
}
.ConsolParah{
  text-shadow: none;
  text-align: justify;
  line-height: 25px;


}
.ConsolText{
	padding: 5%;
}


.Effects{
	transition: 5s ease-in;

	justify-content: center;
	display: flex;
	flex-direction: row;
	height: auto;
	animation: Temp 5s 1;

}

@keyframes Temp {
  from {
	transform: translate(-500px,0);
    opacity: 0;
  }
  to {
	transform: translate(0px,0);
    opacity: 1;
}}
.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.items{
  height: 200px;
  width: 200px;
  background-color: blue;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Headings{

    font-family: "Cairo", Helvetica, Arial, sans-serif;
    letter-spacing: 3px;
    text-align: center;
    color:#ffaa00;
    font-size: 38px;
    line-height: 48px;
    font-weight: 400;
    padding-top: 25px;
}




@media screen and (max-width: 1070px) {
  .Logo{
    height: 50px;
    width: 60px;
  }

  .aboutUsIN{
    flex-direction: column;
    
  }
  .NavList{
    display: none;
  }
  .MenuBtn{
    display: block;
  }
  .FooterBody{

    flex-direction: column;
    
  }

  .FooterAdresses{
    flex-direction: column; 
  }
}

